body {
  margin: 0;
  font-family: "Balsamiq Sans", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.homepage {
    width: 1349px;
    height: 6900px;
    background-color: #ffffff;
}

.Nav {
    background-color: #fff;
    margin-bottom: 16px;
    z-index: 10;
}

.Footer, .Form, .Nav, .Tools-description, .Tools-link {
    font-family: Inter,sans-serif;
}
.Nav-top-logo, .Nav.is-mobile .Nav-mobileAppsLinks a, .Nav.is-mobile .Nav-socialLinks a {
    display: flex;
}

.Footer a, .Nav a, .Tools a {
    text-decoration: none;
}

.u-reset-ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.Nav-link {
    line-height: 24px;
    transition: ease-in-out 200ms color;
}

.Nav-link, .Nav-webApp-link {
    font-size: 16px;
    font-weight: 600;
}

.u-reset-button, .u-reset-input {
    border: 0;
    outline: 0;
}

.u-reset-button {
    background-color: transparent;
    cursor: pointer;
    padding: 0;
}

.Nav-webApp-link {
    align-items: center;
    border-radius: 5px;
    box-sizing: border-box;
    display: flex;
    height: 48px;
    justify-content: center;
}

.Nav-webApp-link--signIn {
    border: 2px solid #7b69c5;
    transition: ease-in-out 200ms border-color,ease-in-out 200ms color;
}

.Nav--sticky {
    box-shadow: 0 0 24px rgba(103,142,178,.16);
    margin: 0 !important;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 20;
}

.Nav-featuresMenu {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 8px;
    max-width: 450px;
}

.Nav--sticky:not(.is-mobile) {
    padding: 16px 45px;
}

.Footer-copyright, .Nav-link, .Tools-description {
    color: #707f8f;
}

.Nav-link :hover{
    color: #162637;
}

.u-m-r-0 {
    margin-right: 0 !important;
}

.Nav--sticky .Nav-webApp-link--signIn {
    color: #162637;
    border: 2px solid #162637;
    margin-right: 8px;
}

.Nav--sticky .Nav-webApp-link {
    padding: 12px 24px;
}

.Footer a, .Nav a, .Tools a {
    text-decoration: none;
}

.Nav-webApp-link--signIn {
    border: 2px solid #162637;
    transition: ease-in-out 200ms border-color,ease-in-out 200ms color;
}

.Nav--sticky .Nav-webApp-link--signUp {
    background-color: #7b69c5;
    color: #fff;
    pointer-events: all;
}

.Nav--sticky .Nav-webApp-link--signUp:hover {
    background-color: #6a5aab;
}

.Nav--sticky .Nav-webApp-link {
    padding: 12px 24px;
}

.Nav--sticky .Nav-webApp-link--signIn {
    color: #162637;
    border: 2px solid #162637;
    margin-right: 8px;
}

.Nav--sticky .Nav-webApp-link--signIn:hover {
    color: #7b69c5;
    border: 2px solid #7b69c5;
}

