.box-image {
    width: 65px;
    height: 65px;
    border-radius: 20px;
    background-color: #b4cbe4;
}

.li-fit-content:hover .box-image{
    transition: ease-in 200ms;
    background-color: #324168;
}

.li-fit-content {
    width: 346px !important;
    height: 90px !important;
    position: unset !important;
    padding: 12px 0px 0px 12px !important;
    cursor: pointer;
    margin-bottom: 12px;
    flex-wrap: wrap;
    display: inline-table;
}

