.react-slideshow-container {
    display: flex;
    width: 45%;
    align-items: center;
    float: right;
    padding-right: 36px;
}

.react-slideshow-container+div.indicators {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 55px;
    height: 376px;
    width: 55%;
}

.indicator .li-fit-content:hover{
    /* background: #ebeeee;
    border-radius: 6px; */
    transition: ease-in-out 200ms all;
}
 
.indicator.active {
    background: #ebeeee;
    transition: ease-in-out 200ms all;
    border-radius: 6px;
    font-weight: 600;
    margin-right: 20px;
}

.indicator.active .li-fit-content .box-image{
    transition: ease-in 200ms;
    background-color: #324168;
}
